body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-header{
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  text-align: center;
  background-color: black;
  color: aliceblue;
  justify-content: center;
  align-items: center;
}

.heading-text{
  color: white;
  font-size: 42px;
  font-weight: bolder;
  text-align: center;
  margin: 20px;
}

.row{
  display: flex;
  flex-direction: row;
  color: aliceblue;
}

.column{
  display: flex;
  flex-direction: column;
}

.jc-center{
  justify-content: center;
}

.white{
  color: aliceblue;
}

.square{
  min-height: 100px;
  min-width: 100px;
  font-size: 64px;
  text-align: center;
  color: #1cdbf5;
  font-weight: bold;
  padding: 30px;
}

.b-bottom-right{
  border-right: 15px solid #1cdbf5;
  border-bottom: 15px solid #1cdbf5;
}

.b-bottom{
  border-bottom: 15px solid #1cdbf5;

}

.b-right{
  border-right: 15px solid #1cdbf5;
}